<template>
	<div class="sj-box">
		<div v-for="item in sj[this.id]">
			<h2 v-text="item.name"></h2>
			<img :src="require('../../../assets/img/'+item.url+'.jpg')" alt="" /><br />
		</div>
		<button class="btn" @click="goBack" href="http://hcx18.com/dqh/">&lt;<span>我的简历</span></button>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: -1,
				sj: [
					[{
							name: "首页",
							url: "sc" + 1
						},
						{
							name: "商品分类",
							url: "sc" + 4
						},
						{
							name: "商品中心",
							url: "sc" + 2
						},
						{
							name: "商品详情",
							url: "sc" + 3
						}
					],
					[{
							name: "首页",
							url: "qy" + 1
						},
						{
							name: "企业产品",
							url: "qy" + 2
						},
						{
							name: "企业简介",
							url: "qy" + 3
						}
					],

				]
			}
		},
		methods: {
			goBack() {
				history.back();
			}
		},
		created() {
			this.id = this.$route.params.id;
			console.log(this.id)
		}
	}
</script>

<style scoped="">
	.sj-box {
		z-index: 1;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #e1eff3;
		overflow: auto;
	}
</style>


