import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Sheji from '@/page/home/children/sheji'

Vue.use(Router)

export default new Router({
	//	mode: 'history',//去掉路径上的#好
	routes: [{
		path: '/',
		name: 'Home',
		component: Home,
		children: [{
			path: 'Sheji/:id', //设计详情页
			component: Sheji,
		}, ]
	}]
})


